.header-wrapper {
	position: absolute;
	z-index: 100;
	width: 90vw;
}

.header__box {
	display: flex;
	justify-content: space-between;

	align-items: baseline;
	padding-top: 2.5rem;
	padding-left: 0.5rem;
	padding-right: 0.5rem;

	.logo {
		cursor: pointer;
		color: $cl-white;
		font-size: 2.5rem;
		font-family: "Alata", sans-serif;
		padding: 0;
		margin: 0;
	}

	.burger {
		cursor: pointer;
	}
}

.menu {
	transform: translateX(600%);
	transition: all 0.3s ease;
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background-color: black;
	color: $cl-white;

	&__list {
		margin-top: 50%;
		margin-left: 2.5rem;
		list-style-type: none;
	}

	&__items {
		margin-top: 2rem;
		text-transform: uppercase;
		font-size: 2.5rem;
		cursor: pointer;
	}
}

.main-title {
	position: absolute;
	top: 50%;
	right: 50%;
	transform: translate(50%, -50%);
	.title-box {
		font-weight: 300;
		color: $cl-white;
		text-transform: uppercase;
		font-size: 4rem;
		border: 2px solid $cl-white;
		padding: 2.5rem;
	}
}

.vr-content {
	width: 100%;
	margin: 0 auto;
	text-align: center;

	.oculus {
		max-width: 100%;
		height: auto;
		display: block;
	}

	&__title {
		color: $cl-black;
		text-transform: uppercase;
		font-weight: 300;
		font-size: 3.3rem;
		padding-top: 5rem;

		line-height: 1;
	}

	&__paragraph {
		color: $cl-grey;
		font-weight: bold;
		font-size: 1.5rem;
		line-height: 1.6;
		padding: 2.4rem;
	}
}

.grid-gallery {
	width: 100%;
	text-align: center;
	&__title {
		color: $cl-black;
		text-transform: uppercase;
		font-weight: 300;
		font-size: 3.3rem;
		padding-top: 5rem;

		line-height: 1;
	}

	&__images {
		display: grid;

		grid-template-columns: 1fr;
		
	}

	&__picture {
		max-width: 100%;
	}
}
